body{
 background: #005C97;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #363795, #005C97);  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-gradient(linear, right top, left top, from(#363795), to(#005C97));
background: -webkit-linear-gradient(right, #363795, #005C97);
background: -o-linear-gradient(right, #363795, #005C97);
background: linear-gradient(to left, #363795, #005C97); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.App{
  width: 480px;
  margin: 100px auto;
  background: #f7f7f7;
  -webkit-box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
}

.App h1{
  background: #2090b9;
  color: #f4f4f4;
  margin: 0;
  padding: 10px 20px;
  text-transform: uppercase;
  font-size: 24px;
  font-width: 400;
}
.inputBar{
  font-size: 18px;
  background-color: #f7f7f7;
  width: 100%;
  padding: 13px 13px 13px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 3px solid rgba(0, 0, 0, 0);
}
.inputBar:focus{
  background: white;
  border: 3px solid #2980b9;
}
.optionsBoxes{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.btn{
  height: 40px;
  width: 33%;
  font-size: 16px;
  border: none;
  background: inherit;
  text-align: center;
}
.btn:focus{
  outline: none;
}
.showAllBTN{
  background: rgba(0, 255, 0, 0.2);
}
.doneBTN{
  background: rgba(0, 0, 255, 0.2 );
}
.deleteDoneBTN{
  background: rgba(255, 0, 0, 0.2);
}
.showAllBTN:focus{
  background: rgba(0, 255, 0, 0.3);
}
.doneBTN:focus{
  background: rgba(0, 0, 255, 0.3);
}
.deleteDoneBTN:focus{
  background: rgba(255, 0, 0, 0.3);
}
.list{
  list-style: none;
  margin: 0;
  padding: 0;
}
.taskListItem{
  background: #fff;
  min-height: 40px;
  line-height: 40px;
  color: #fff;
  font-size: 20px;
  font-weight: 500;
}
.taskListItem:nth-child(2n){
  background: #f7f7f7;
  color: #f7f7f7;
}
.taskListItem:hover .deleteBTN{
  width: 40px;
  background: #e74c3c;
  color: #f4f4f4;
}
.deleteBTN{
background: inherit;
height: 40px;
margin-right: 20px;
text-align: center;
width: 40px;
display: inline-block;
color: inherit;
-webkit-transition: 0.2s linear;
-o-transition: 0.2s linear;
transition: 0.2s linear;
}
.taskText{
  display: inline;
  color: #333 ;
}
.checked{
  /*color: #a5a5a5;*/
  text-decoration: line-through;
}
.done{

}
.hide{
  display: none;
}
.unhide{
  display: inline-block;
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



